.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

h1.App__title, h2.App__title, h3.App__title  {
  font-weight: bold;
  font-size: 2em;
  text-align: center;
}

h1.App__titleSmall, h2.App__titleSmall, h3.App__titleSmall {
  font-size: 1em;
}

.App__thinTitle {
  font-weight: 200;
}
/*
 *   Copyright (c) 2021 Matthieu Leperlier
 *   All rights reserved.
 */
.Generator {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 1em 25vmin;
  
}

.Generator__button {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  text-transform: lowercase;
  background-color: transparent;
  border-width: thick;
  border-color: black;
  border-radius: 1000px;
  padding: 0.25em 0.5em;
  margin: 1em;
}

.Generator__resultsList {
  list-style: none;
  display: flex;
  justify-content: center;
  margin: 0.5em;
  margin-bottom: 2em;
}

.Generator__resultsItem {
  display: flex;
  flex-direction: column;
  margin: 0 0.5em;
}

.Generator__resultsItemCode {
  font-size: 0.5em;
}

.Generator__resultsCode {
  background-color: whitesmoke;
  color: rgba(0, 0, 0, 0.75);
  border-color: whitesmoke;
  border-width: medium;
  border-style: solid;
  border-radius: 5px;
  padding: 0.5em;
  margin: 1em;

  font-weight: 200;
}

.Generator__resultsOptions {
  display: flex;
  justify-content: center;
  margin: 1em;
  font-size: 0.8em;
}

.Generator__optionsFieldset {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 1em;
}

.Generator__optionsFieldsetLegend {
  /*margin: 1em;*/
  margin-bottom: 1em;
}

.Generator__optionsWrapper {
  display: flex;
  align-items: center;
}

.Generator__optionsWrapper label {
  margin-left: 0.1em;
}

.Generator__optionsWrapper label:not(:last-of-type) {
  margin-right: 0.5em;
}
