 .About {
   display: flex;
   flex-direction: column;
   margin: 1em  50vmin;
   padding: 1em;
   border-radius: 5px;
   background-color: whitesmoke;
 }

 .About__title {
   text-transform: lowercase;
   margin-bottom: 1em;
 }

